<template>

  <div class="main">

    <div v-for="(item, index) of list_datas" class="main_list" v-viewer>
      <PicDataMotify :obj="item"
                     @click_base="pic_update"
                     @updateAction="updateAction"
                     @click_tag_add="tag_add"
                     @click_tag_remove="tag_remove"

      />
    </div>
  </div>
</template>

<script>

    import PicDataMotify from "./PicDataMotify";


    export default {
        name: "ManageMotify",
        components: {PicDataMotify},
        data() {
            return {
                list_datas: [],
                list_old_ids: [],
                sql_pic: this.Common_Base.SQL_Pic
            }
        },
        mounted() {
            const query = Bmob.Query(this.sql_pic);
            query.order("-last_date");
            query.find().then(res => {
                // console.log(res)
                // res=res.reverse()
                for (let item of res) {
                    let urls = item.url.split(",")
                    let tags0 = []

                    if (typeof (item.tags) != 'undefined') {
                        if (item.tags != '') {
                            tags0 = item.tags.split(",")
                        }
                    }

                    this.list_datas.push({
                            'urls': urls,
                            'description': item.description,
                            'url0': urls[0],
                            'tags0': tags0,
                            'id': item.objectId
                        }
                    );
                    this.list_old_ids.push(item.objectId)


                    // this.list_datas.push({'url': item.url, 'text': item.description})
                }

            }).catch(error => {
                console.log(error)
            })
        },
        methods: {
            updateAction(item) {
                var query = Bmob.Query(this.sql_pic);
                query.set('id', item.id);
                query.set('url', item.urls.join(","));
                query.set('tags', item.tags0.join(","));
                query.set('description', item.description);
                query.save().then(res => {
                    // console.log(res)
                    this.$message('修改完成');
                }).catch(err => {
                    this.$message(err);
                })
            },

            pic_update(obj) {
                console.log(obj)
                var query = Bmob.Query(this.sql_pic);
                //网络 删除
                query.destroy(obj.id).then(res => {
                    // console.log(res)
                    this.list_datas.remove(obj)
                }).catch(err => {
                    // console.log(err)
                    this.$message(err);
                })


            },
            tag_add(obj, tag) {
                let index = this.list_datas.indexOf(obj);
                if (index <= -1) {
                    console.log('tag_add index error')
                    return
                }
                let real_obj = this.list_datas[index];

                if (real_obj.tags0.indexOf(tag) > -1) {
                    console.log('已存在' + tag)
                    return
                }
                real_obj.tags0.push(tag)
            },
            tag_remove(obj, tag) {
                let index = this.list_datas.indexOf(obj);
                if (index <= -1) {
                    console.log('tag_remove index error')
                    return
                }
                let real_obj = this.list_datas[index];
                real_obj.tags0.remove(tag)
            },
        }
    }
</script>

<style lang="stylus" scoped>

  .main
    display flex
    flex-direction column
    align-items center
    padding 1rem

    .main_list

      width 80%


</style>



