<template>

  <div>

    <!--<div id="myChart" :style="{width: '500px', height: '300px'}"></div>-->
    <v-chart class="my-chart" :options="bar"/>
  </div>
</template>

<script>

  import ECharts from 'vue-echarts'
  import 'echarts/lib/chart/line'

  export default {
    name: "BaseDateShow",
    data() {
      return {

        last_location: '',
        dates:[],
        counts:[],
        bar:{
          tooltip: {
            trigger: 'axis'
          },
          xAxis: {
            type: 'category',
            data:[]
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [],
            type: 'line'
          }]
        }
      }
    },
    comments:{
      "v-chart": ECharts
    },
    mounted() {


      const query = Bmob.Query(this.Common_Base.SQL_Pahsa_Base);
      query.find().then((res) => {

        for (let item of res) {
          this.bar.xAxis.data.push(item['date'])
          this.bar.series[0].data.push(item['count'])
        }

        // // this.showCharts()
        // this.bar.xAxis.data.push(this.dates)
        // // console.log( this.bar.series)
        // this.bar.series[0].data.push(this.counts)

      })

    },
    methods: {
      showCharts() {

        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('myChart'))

        let option = {
          tooltip: {
            trigger: 'axis'
          },
          xAxis: {
            type: 'category',
            data:this.dates
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: this.counts,
            type: 'line'
          }]
        };

        // 绘制图表
        myChart.setOption(option);

      }
    }
  }
</script>

<style scoped>

  .item_main {
    display: flex;
  }

  .my-chart {
    width: 800px;
    height: 500px;
  }

</style>



